<template>
  <div class="about">
    <div v-if="isShow">
      <!-- <p>
        请输入名称: <input type="text" v-model="user">  
      </p>
      <p>
        请输入密码: <input type="text" v-model="pass">  
      </p>
      <button @click="login">登陆</button> -->
      <van-cell-group>
        <van-field
          v-model="user"
          required
          clearable
          label="请输入名称:"
          placeholder="请输入名称"
        />

        <van-field
          v-model="pass"
          type="password"
          label="请输入密码"
          placeholder="请输入密码"
          required
        />
      </van-cell-group>
      <van-button type="primary" size="large" @click="login">登陆</van-button>
    </div>
    <div v-else>
      <van-cell-group>
        <van-cell title="名称为:" :value="users" />
      </van-cell-group>
      <div  style="text-align:center;padding-top:20px;">
        <van-button icon="https://img.yzcdn.cn/vant/logo.png" @click="logout" type="info">退出登陆</van-button>
      </div>
      
      <!-- <van-button round  type="primary" size="large" @click="logout">退出登陆</van-button> -->
      <!-- <p>
