      </button> -->
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      user:"",
      pass:"",
      users:localStorage.getItem("user"),
      isShow:localStorage.getItem("user") ? false : true
    }
  },
  methods: {
    login(){
      this.isShow = false;
      this.users = this.user;
      localStorage.setItem("user" , JSON.stringify(this.user));
      
    },
    logout(){
      localStorage.clear();
      this.isShow = true;
      this.users = ""
    }
  }
}
</script>